<style scoped>
 .main{
   display: flex;
   flex-direction: column;
 }
  .header{
    height: 56px;
  }
  .conter{
    width: 100%;
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    position: relative;
    user-select: none;

  }
  .forumItem{
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 12px;
    padding-right: 12px;
    border-bottom: solid #eee 10px;
  }
 .forumItem .top{
   display: flex;
 }
 .forumItem .top .avatar{
   width: 50px;
   display: flex;
   justify-content: center;
   align-items: center;
 }
 .forumItem .top .tmes{
   flex: 1;
   padding-left: 5px;
 }
 .forumItem .top .title{
   font-size: 0.30rem;
 }
 .forumItem .top .tidis{
   font-size: 0.22rem;
   color: #999;
 }
 .forumItem .content{
   margin-top: 10px;
   margin-bottom: 10px;
 }

 .forumItem .file{
  display: flex;
   align-items: center;
   color: #009688;
   margin-bottom: 10px;
 }
 .forumItem .file span{
   padding-left: 10px;
   text-decoration-line: underline;
 }
  .forumItem .bottom{
    display: flex;
    justify-content: center;
    align-items: center;
  }
 .forumItem .bottom .love{
   width: 50px;
   height: 50px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   padding: 5px;
   background-color:#009688;
   border-radius: 50%;
   color: #fff;
 }

 .comment{
   padding-left: 12px;
   padding-right: 12px;
   padding-top: 10px;
 }
 .comment .comtitle{
   font-size: 0.28rem;
 }
 .comment .com_item{
   margin-top: 10px;
 }
 .comment .com_item .com_itemtop{
   display: flex;
   align-items: center;
 }
 .comment .com_item .com_itemtop span{
   padding-left: 10px;
 }
 .comment .com_item  .com_itemcontent{
   margin-top: 10px;
   margin-bottom: 10px;
   text-indent:0.5rem;
 }
</style>
<template>
  <div class="main">
    <div class="header">

    <mu-appbar :title="title" style="text-align: center">
      <mu-icon-button icon="navigate_before" slot="left" @click="back"/>
      <mu-icon-menu icon="more_vert" slot="right">
        <mu-menu-item title="全部" />
        <mu-menu-item title="精华" />
      </mu-icon-menu>
    </mu-appbar>
    </div>
    <div class="conter" id="scoll">
      <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/>
      <div class="forumItem">
          <div class="top">
            <div class="avatar"><mu-avatar slot="left" icon="assignment_ind" backgroundColor="#ddd" :size="40"/></div>
            <div class="tmes">
              <div class="title">
                <span style="color: #009688;padding-right: 10px">{{card.fdName}}</span>
                {{card.docSubject}}</div>
              <div class="tidis">{{card.docAlterTimeStr}} 阅读：50 收藏：{{card.fdCollectionCount}} </div>
            </div>
          </div>
          <div class="content" v-html="card.docContent">
          </div>
          <div class="file" v-for="i in card.cList">
            <mu-icon value="insert_drive_file"/>
            <span>{{i.fdFileName}}</span>
          </div>
          <div class="bottom">
            <div class="love">
              <mu-icon value="favorite"/>
              <span>{{card.fdCollectionCount}}</span>
            </div>
          </div>
      </div>
      <div class="comment">
              <div class="comtitle">评论（{{comments.length}}）</div>
              <div class="com_item" v-for="item in comments">
                      <div class="com_itemtop">
                        <mu-avatar slot="left" icon="assignment_ind" backgroundColor="#ddd" :size="30"/>
                        <span>{{item.fdName}}</span> <span style="color: #999">回复</span> <span>{{item.pName}}</span>
                        <span style="color: #888">{{item.docCreateTimeStr}}</span>
                      </div>
                      <div class="com_itemcontent">
                        {{item.docSummary}}
                      </div>
                <mu-divider/>
              </div>
      </div>
    </div>
  </div>
</template>
<script>

  export default {
    data () {

      return {
        title:"文化活动",
        refreshing:false,
        trigger: null,
        fdid:"",
        card:{},//帖子
        comments:[]//评论
      }
    },
    mounted:function () {
      this.fdid = this.$route.query.id;
      this.trigger = document.getElementById("scoll");
      this.getData();

    },
    methods: {
      getData:function () {
        let self = this;
        var url =self.path+ 'findForumPostList.json'+'?token='+self.token+"&pageSize=10&page=1&fdTopicId="+self.fdid;
        console.log(url);
        self.$http.get(url
        ).then((response) => {
          console.log(response);
          self.card = response.data.dataInfo.listData[0];
          self.card .docContent = self.card .docContent.replace(/src="/g ,'style="width:30vw;height:30vw;display:block;" src="http://ekp.sunwoda.com');

          if(response.data.dataInfo.listData.length>0){
            for(var i =1;i<response.data.dataInfo.listData.length;i++){
              self.comments.push(response.data.dataInfo.listData[i])
            }
          }
          console.log(self.card);
          console.log(self.comments)
          if(response.data.statusCode !=0){
            alert("暂无数据");
            return 0
          }
        }, (response) => {
          console.log('error');
        });
      },
      refresh:function () {
        console.log("bbb");
        this.refreshing=true
      },
      back:function () {
        this.$router.go(-1)
      },
    }
  }
</script>



