<style scoped>
    .main{
      /*height: 100%;*/
    }
    .mu-infinite-scroll{
      padding-bottom: 0;
    }
</style>
<template>
  <div class="main">
   <forum-lall :listForum="listForum"></forum-lall>
    <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/>
  </div>
</template>
<script>
  import forumLall from "@/components/page/forum/forumcom/forumLall"
  export default {
    data () {
      return {
         listForum:[],
         loading: false,
         scroller: null,
         totalPage:0,
         startPage:1,
      }
    },

    mounted:function () {
      this.startPage=1;
       this.getdata(this.startPage);
    },
    methods: {
      loadMore () {
        this.loading = true;
        if(this.startPage<this.totalPage){
          this.startPage++;
          this.getdata(this.startPage)
        }else {
          this.loading = false;
        }

      },
      getdata:function(page){
        let self = this;
        var url =self.path+ 'findTopicList.json'+'?token='+self.token+"&pageSize=10&page="+page+"&fdPinked=2";
        console.log(url);
        self.$http.get(url
        ).then((response) => {
          console.log(response);
           self.loading = false;
          if(response.data.statusCode !=0){
            alert("暂无数据");
            return 0
          }
          self.totalPage = response.data.dataInfo.pageInfo.totalPage;
          self.listForum =  self.listForum.concat(response.data.dataInfo.listData);
          self.scroller = document.getElementById("scoll");
        }, (response) => {
          console.log('error');
        });

      }
    },
    components: {
      forumLall
    }
  }
</script>



